
.header {          
    
    .header-tip {
        @top-offset: 21px;
        padding-top: @top-offset;
        padding-bottom: 4px;
        text-align: center;
        position: relative;
        .container();
        
        ul:extend(.list-inline all){
            position: absolute;
            top: @top-offset;

            &.left-list {
                left: 0;
            }
            &.right-list {
                right: 0;
            }
        }
    }

    .logo-heading {
        color: @text-color;
        display: inline-block;

        .logo-text {
            position: relative;
            font-size: 24px;
            margin-bottom: 5px;
            margin-top: 0;
            line-height: 1;
            font-family: @font-family-serif;
        }

        > .hgroup {
            margin-top: 12px;
            margin-bottom: 18px;
            width: 196px;
            .heading {
                font-family: serif;
                text-transform: uppercase;
                font-size: 11px;
                > span {
                    padding: 0 6px;
                }
            }
        }

        &:hover {
            text-decoration: none;
            color: @text-color;
        }
    }
    
    .navbar.affix {
        top: 0;
        left: 0;
        width: 100%;
        z-index: @zindex-navbar;
        // chrome fix
        -webkit-backface-visibility: hidden;
        -webkit-transform: translateZ(0);
    }
}

.top-alert {
    text-align: center;
    padding: 10px 0;
    background-color: lighten(@brand-primary, 20%);
    color: #fff;
}

.main-navbar {    
    .navbar-nav {
        &, li {
            float: none;
            text-align: center;
        }
        & > li {
            display: inline-block;
        }
        .dropdown-menu {
            text-align: left;
        }
    }
}


.social-links {
     a {
        display: inline-block;
        zoom: 1;
        height: 0;
        text-indent: -999em;
        vertical-align: middle;
        width: 22px;
        padding-top: 22px;
        background: url('../img/social-links.png') 0 0 no-repeat;
        .opacity(0.3);

        &:hover {
            .opacity(1);
        }
    }
    .facebook a {
    }
    .twitter a {
        background-position: -22px 0;
    }
    .instagram a {
        background-position: -110px 0;
    }       
}


.hgroup {
    position: relative;
    height: 1px;
    width: 240px;
    text-align: center;
    background: #858585;
    font: 0/0 serif;
    margin: 20px auto;
    
    .heading {
        position: absolute;
        top: -0.5em;
        line-height: 1;
        left: 0;
        right:0;
        margin: auto;

        > span {
            padding: 0 12px;
            background: @body-bg;
        }
    }
}
