
.featurette {
    padding-top: 40px;
    padding-bottom: 40px;

    p {
        padding-left: 25px;
        padding-right: 25px;
    }

    .hr {
        width: 180px;
        margin: 25px auto;
        height: 0;
        font: 0/0 serif;
        border: 0;
        background: none;
        border-top: 1px solid #000;
        .center-block();
    }
}

.featurette-content:extend(.container all) {
    position: relative;
}

.featurette-satin {
   background-color: #fff;
   p {
     color: #575757;
   }
}

.featurette-black {
   background-color: #000;
   color: #fff;
   .hr {
       border-color: #fff;
   }
   p {
     color: #f9f9f9;
   }   
}

.service-item {
    .text-center();
    p {
        margin-left: 50px;
        margin-right: 50px;
    }    
    .btn {
        font-size: 10px;
        text-transform: uppercase;
    }
}
.service-heading {
    display: block;
    margin: 0 25px 25px 25px;
    padding-bottom: 25px;
    border-bottom: 3px solid #000;
    color: @text-color;
    font-size: 15px;
    line-height: 1;
    font-family: @headings-font-family;
    text-transform: uppercase;
    cursor: pointer; 
}
.service-thumb {
    .center-block();

    position: relative;
    overflow: hidden;
    margin-bottom: 25px;
    
    &, img {
        .size(270px, 225px);
    }
}

.divider {
    position: absolute;
    background: #d6d6d6;
    display: block;
    width: 1px;
    top: 0;
    height: 100%;
}
.divider-4 {
   left: unit((100 / 3), %);
}
.divider-8 {
   left: unit((2 * 100 / 3), %);
}

.feature-heading {
    text-align: center;
    margin-bottom: 0;
}

.single-paragraph {
    width: 520px;
    .center-block();
    .text-center();
}