.box-desc {
	padding: .6em 1em;
	color: @gray-light;
	border: 1px solid #e5e5e5;
}

.product-detail {
	padding: 0 (@grid-gutter-width / 2);
	.product-name {
		margin-top: 0;
	}
	.product-main-img {
		.center-block();
	}
}

.products-containter {
    background-color: #fff;
}

.container-md {
    width: 1200px;
    .center-block();
}

.contact-box {
    padding-top: 5px;
    background-color: #000;
    color:#fff;
    text-align: center;
    margin-top: -@navbar-margin-bottom;
    
        .google-maps-trigger {
        margin-bottom: 25px;
        display: inline-block;
        font-size: 40px;
        line-height: @line-height-computed;
        cursor: pointer;
        transition: all 0.2s ease-out;
        
        &:hover {
            .opacity(0.9);
        }
    }
}

.map-box {
    padding: 40px;
}

.box {
    border: 3px solid;
}