.thumb-grid {
	.clearfix();
    margin-bottom: @line-height-computed;
    
    .thumb {
	    .pull-left();
        .size(235px, 300px);

        overflow: visible;
        position: relative;
        border-style: solid;
        border-color: #f0f0f1;
        border-width: 1px;
        margin-top: -1px;
        margin-left: -1px;
        background: #fff;

	}	
	.thumb-img {
        padding: 40px 40px 0 40px;
        
        img {
            .center-block();
            margin: 0;
            padding: 0;
            line-height: 150px;
            max-height: 150px;
            max-width: 150px;
        }
	}

	.thumb-desc {
		margin-top: 15px;
        padding: 0 30px;
		line-height: 18px;

        .thumb-desc-title {
		    font-weight: 700;
            font-size: 0.9em;
            text-transform: uppercase;		
	    }		

	    .thumb-desc-brand {
	       color: #888;
           font-size: 0.9em;
	    }
	}
}

.package-grid {
   .make-row(@grid-gutter-width);
  
    .package {
		.make-sm-column (4, 0);
		margin-bottom: (2 * @line-height-computed);
		text-align: center;
		font-size: 1.0833em;
	}
    .package-name {
		text-transform: uppercase;
		text-align: center;
		letter-spacing: 1px;
        font-weight: 500;
	}	

    .package-desc {
        position: relative;
        height:  (@line-height-computed * 2);
        line-height: 16px;
        color: @gray-light;
        width: 250px;
        text-align: center;
        overflow: hidden;
        .center-block();


        .package-desc-shadow {
            position: absolute;
            bottom: 0;
            .size(100%, @line-height-computed);
            #gradient > .vertical(@start-color: transparent; @end-color: #fff);
        }
    }
}
